.new-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    padding: 0.5rem;
    margin: 1rem 0;
    border-radius: 12px;
    background-color: #5f5f5f;
  }
  
  .new-item__description {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
  }
  
  .new-item h2 {
    color: #000000;
    font-size: 1rem;
    flex: 1;
    margin: 0 1rem;
    color: rgb(0, 0, 0);
  }
  
  .new-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #001d5d;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
  }
  
  @media (min-width: 580px) {
    .new-item__description {
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      flex: 1;
    }
  
    .new-item__description h2 {
      font-size: 1.25rem;
    }
  
    .new-item__price {
      font-size: 1.25rem;
      padding: 0.5rem 1.5rem;
    }
  }